.login-signup{
    width: 100%;
    height: 120vh;
    background: #fce3fe;
    padding-top: 50px;
}

.login-signup-container{
    width: 580px;
    min-height: 550px;
    background: white;
    margin: auto;
    padding: 40px 60px;
}

.login-signup-container h1{
    margin-top: 20px 0px;
}

.login-signup-fields{
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 30px;
}

.login-signup-fields input{
    height: 52px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}

.login-signup-container button{
    width: 580px;
    height: 52px;
    color: white;
    background: #FF4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}

.login-signup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

.login-signup-login span{
    color: #FF4141;
    font-weight: 600;
}

.login-signup-agree{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}